import React from 'react'

const tableStyle ={
    width: 200
}

const List = props => (
    <ul>
        <table>
        {
            props.items.map((item,index) => 
                <tr>
                    <li key={index}>
                        <td style={tableStyle}>{item}</td>
                        <td><button>Delete</button></td>
                    </li>
                </tr>
            )
        }
        </table>
    </ul>
)

export default List